<template>
  <div class="SunView">
    <div class="drag"     
    @mousedown="dragStart"
    @mousemove="dragMove"
      @mouseup="dragEnd"
    :style="{ transform: 'translate(' + offsetX + 'px, ' + offsetY + 'px)' }">>
      <div class="planetContainer"  @wheel="zoomContainer"
      ref="planetContainer" >
      <div class="planet" v-for="planet in planets" :key="planet.name" >
        <img :src="planet.image" :alt="planet.name" :style="{ width: planet.size, height: 'auto' }" @mouseover="showTextbox(planet.name)" @mouseleave="hideTextbox" />
        <div class="tooltip" v-show="isTextboxVisible && currentPlanet === planet.name" >&ensp;&ensp;&ensp;&ensp;{{ planet.description }}</div>
      </div>
    </div>
   </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isTextboxVisible: false,
      currentPlanet: null,
        // 
      offsetX : 0,
      offsetY : 0,
      initialX : 0,
      initialY : 0,
      isDragging : false,
      scale : 1.0,
      zoomIncrement : 0.1,
      draggableElement:document.getElementsByClassName('drap'),
      planets: [
      { name: '', image: require('@/assets/.png'), description: '87.9691116437C-172C1301.5', size:'100%' },
      { name: '', image: require('@/assets/.png'), description: 'Venus224.7Venus', size:'100%' },
      { name: '', image: require('@/assets/earth.png'), description: 'EarthTerra1.5 29.2%  70.8% ,', size:'100%' },
      { name: '', image: require('@/assets/.png'), description: 'MarsMartis' , size:'100%'},
      { name: '', image: require('@/assets/.png'), description: 'Jupiter2.5172023292', size:'100%' },
      { name: '', image: require('@/assets/-m.png'), description: 'SaturnSaturnus1800/2023145 ', size:'100%' },
      { name: '', image: require('@/assets/-1.png'), description: 'Uranus18.37~20.082178131327513' , size:'100%'},
      { name: '', image: require('@/assets/.png'), description: 'Neptune2400km/h-21855K7000', size:'100%' },
      { name: '', image: require('@/assets/.png'), description: 'Pluto1343409101/61/3', size:'100%' },
      { name: '', image: require('@/assets/moon.png'), description: 'MoonLuna1737.100.2737.342100.012338.4430', size:'100%' },

    ]
    };
  },
  methods: {
    showTextbox(planet) {
      this.isTextboxVisible = true;
      this.currentPlanet = planet;
    },
    hideTextbox() {
      this.isTextboxVisible = false;
      this.currentPlanet = null;
    },
   



  // 
  dragStart(event) {
    this.isDragging = true;
      this.initialX = event.clientX - this.offsetX;
      this.initialY = event.clientY - this.offsetY;
  },

  // 
  dragMove(event) {

    if (this.isDragging) {
        // 
        this.offsetX = event.clientX - this.initialX;
        this.offsetY = event.clientY - this.initialY;
      }
  },
  dragEnd() {
      this.isDragging = false;
    },
  
  // 
  mouseUP() {
    this.isDragging = false;
  },

  // 
  zoomContainer(event) {
    event.preventDefault();
    
    console.log(event.deltaY )
      const delta = event.deltaY 

      // 
      if (delta > 0) {
        this.scaleContainer(0.9); // 
      } else {
        this.scaleContainer(1.1); // 
      }
    },
    scaleContainer(scaleFactor) {
      const container = this.$refs.planetContainer;
      const currentScale = parseFloat(container.style.transform.replace('scale(', '')) || 1;
      const newScale = currentScale * scaleFactor;

      // 
      if (newScale >= 0.1 && newScale <= 3) {
        container.style.transform = `scale(${newScale})`;
      }
    }
  }

  }

</script>

